body{
    background-color: whitesmoke;
}

.meme-form{
    position: relative;
    top:-20px;
    padding: 50px;
    margin: 30px;
}

.meme-form > input{
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    color: black;
    width: 20%;
    height: 40px;
    font-size: 25px;
}
.meme-form > button{
    background-color: red;
    color: white;
    border-radius: 10px;
    border: none;
    width: 200px;
    height: 50px;
    position: relative;
    left: 30px;
    top: 10px;
    font-size: 25px;
}

.img-meme{
    position: relative;
    top: -300px;
    left: 50%;
    width: 500px;
    height: 300px;
    
}

.img-meme >img{
    width:100%;
    height:100%;
}

.top{
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
        top:0px;
    
}

.bottom{
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
        top: 80%;
    
}